<template>
  <div class="layout-px-spacing">
    <ul class="navbar-nav flex-row mt-2">
      <li>
        <div class="page-header">
          <nav class="breadcrumb-one" aria-label="breadcrumb">
            <ol class="breadcrumb">
              <li class="breadcrumb-item"><a href="javascript:;">{{ approvalGroup.group }}</a></li>
              <li class="breadcrumb-item active" aria-current="page">
                 Purchase / Payment Approal Chain 
              </li>
            </ol>
          </nav>
        </div>
      </li>
    </ul>


<global-search-field
v-model:filterValue="filter"
/>
<div class="d-flex align-items-center justify-content-between mb-3 mt-3">

  <router-link to="/requisitions/all" class="btn btn-primary">
    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-box-arrow-in-left" viewBox="0 0 16 16">
      <path fill-rule="evenodd" d="M10 3.5a.5.5 0 0 0-.5-.5h-8a.5.5 0 0 0-.5.5v9a.5.5 0 0 0 .5.5h8a.5.5 0 0 0 .5-.5v-2a.5.5 0 0 1 1 0v2A1.5 1.5 0 0 1 9.5 14h-8A1.5 1.5 0 0 1 0 12.5v-9A1.5 1.5 0 0 1 1.5 2h8A1.5 1.5 0 0 1 11 3.5v2a.5.5 0 0 1-1 0z"/>
      <path fill-rule="evenodd" d="M4.146 8.354a.5.5 0 0 1 0-.708l3-3a.5.5 0 1 1 .708.708L5.707 7.5H14.5a.5.5 0 0 1 0 1H5.707l2.147 2.146a.5.5 0 0 1-.708.708z"/>
    </svg>
    Requisitions
  </router-link>

  <h3 class="text-center m-0 flex-grow-1" v-if="!mobileView">
    {{ date.toUpperCase() }}
    {{
      approvalGroup?.group?.toLowerCase().includes('labour')
        ? "LABOUR REQUEST"
        : approvalGroup?.group?.toLowerCase().includes('voucher')
          ? "PAYMENT VOUCHERS"
          : "LOCAL PURCHASE ORDERS"
    }}
  </h3>

  <router-link to="/good-received-notes/dashboard" class="btn btn-secondary">
    GRN
    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-arrow-right-square" viewBox="0 0 16 16">
      <path fill-rule="evenodd" d="M15 2a1 1 0 0 0-1-1H2a1 1 0 0 0-1 1v12a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1zM0 2a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v12a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2zm4.5 5.5a.5.5 0 0 0 0 1h5.793l-2.147 2.146a.5.5 0 0 0 .708.708l3-3a.5.5 0 0 0 0-.708l-3-3a.5.5 0 1 0-.708.708L10.293 7.5z"/>
    </svg>
  </router-link>

</div>


<div class="mb-3 d-flex justify-content-end align-items-center gap-2">
  <div style="display: inline-block; min-width: 200px;">
    <multiselect 
      v-model="status"
      :options="statuses"
      placeholder="Select Status"
      label="name"
      :searchable="true"
      :multiple="false"
      :required="true"
      :allow-empty="true"
    />  
  </div>
  <input type="date" class="form-control w-auto" v-model="date">
</div>



<div class="d-flex justify-content-end flex-wrap gap-2">
  <button class="btn btn-success" @click="printReport()">
    <svg xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="currentColor" class="bi bi-printer" viewBox="0 0 16 16">
      <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1"/>
      <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1"/>
    </svg>
  </button>

  <button class="btn btn-dark" @click="date='all',getApprovalItems()">Fetch {{ status?.name ?? 'All' }} Data</button>

  <button 
    class="btn btn-primary" 
    @click="getApprovalItems()"
  >
    Refresh
  </button>
</div>



    <div class="row layout-top-spacing">
      <div v-if="loading" v-html="tableShimmer()"></div>
      <div 
      v-else
      class="col-xl-12 col-lg-12 col-sm-12 layout-spacing">
        <div class="panel p-0">
          <div class="custom-table table3">
            <v-client-table :data="filteredData" :columns="columns" :options="table_option">

              <template #status="props">
                <span class="badge inv-status" :class="'badge-' + props.row.status.color">
                  {{ props.row.status.name }}
                </span>
              </template>
              <template #requisition="props">
                 <router-link
                 v-if="props.row.type === 'Labour Request'"
                 class="text-primary" :to="`/approval/${props.row.requisition}`" title="Click to see requisition">{{ props.row.requisition }}</router-link>
                 <router-link 
                 v-else
                 class="text-primary" :to="`/requisition/${props.row.requisition}`" title="Click to see requisition">{{ props.row.requisition }}</router-link>
              </template>
              <template #actions="props">
                <a
                  @click="getApprovalChain(props.row)"
                  data-bs-toggle="modal"
                  data-bs-target="#approval"
                  href="javascript:void(0);"
                  class="text-primary"
                >
                  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-eye" viewBox="0 0 16 16">
                    <path d="M16 8s-3-5.5-8-5.5S0 8 0 8s3 5.5 8 5.5S16 8 16 8M1.173 8a13 13 0 0 1 1.66-2.043C4.12 4.668 5.88 3.5 8 3.5s3.879 1.168 5.168 2.457A13 13 0 0 1 14.828 8q-.086.13-.195.288c-.335.48-.83 1.12-1.465 1.755C11.879 11.332 10.119 12.5 8 12.5s-3.879-1.168-5.168-2.457A13 13 0 0 1 1.172 8z" />
                    <path d="M8 5.5a2.5 2.5 0 1 0 0 5 2.5 2.5 0 0 0 0-5M4.5 8a3.5 3.5 0 1 1 7 0 3.5 3.5 0 0 1-7 0" />
                  </svg>
                </a>

                <a 
                v-if="user_permissions.includes('can_print_lpo')  && (props.row?.status?.name === 'approved' || props.row?.status?.name === 'paid')"
                href="javascript:void(0);" @click="printGeneralOrder(props.row,props.row.vendor_data)" class="text-success me-2">
  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-printer" viewBox="0 0 16 16">
    <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z"/>
    <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2H5zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4V3zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2H5zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1z"/>
  </svg>
                </a>

                <a 
                v-if="user_permissions.includes('can_print_lpo') && (props.row?.status?.name === 'approved' || props.row?.status?.name === 'paid') "
                href="javascript:void(0);" @click="downloadVoucherPDF(props.row,props.row.vendor_data)" class="text-dark">
<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-download" viewBox="0 0 16 16">
  <path d="M.5 9.9a.5.5 0 0 1 .5.5v3.5a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1v-3.5a.5.5 0 0 1 1 0v3.5a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2v-3.5a.5.5 0 0 1 .5-.5"/>
  <path d="M7.646 11.854a.5.5 0 0 0 .708 0l3-3a.5.5 0 0 0-.708-.708L8.5 10.293V1.5a.5.5 0 0 0-1 0v8.793L5.354 8.146a.5.5 0 1 0-.708.708z"/>
</svg>
                </a>

                <a 
                v-if="user_permissions.includes('can_delete_lpo')"
                href="javascript:void(0);" @click="deleteApproval(props.row)" class="text-danger">
<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-trash" viewBox="0 0 16 16">
  <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5m2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5m3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0z"/>
  <path d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4zM2.5 3h11V2h-11z"/>
</svg>
                </a>

   
                <!-- modal trigger only -->
              </template>
            </v-client-table>
          </div>
        </div>
      </div>
    </div>
  </div>

  <!-- Modal -->
  <div
    class="modal fade mymodal"
    id="approval"
    tabindex="-1"
    role="dialog"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
    data-bs-backdrop="false" 
    ref="approvalModal"
  >
    <div class="modal-dialog modal-fullscreen" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel" >Approval Chain  </h5>
          <button type="button" data-dismiss="modal" data-bs-dismiss="modal" aria-label="Close" class="btn-close" @click="clearForm()" id="close-approval-modal"></button>
        </div>

             <div class="col-xl-12 col-lg-12 col-sm-12 layout-spacing">
                <div class="panel p-0">
                    <div class="custom-table table3 table-responsive">
<table class="table table-bordered table-striped">
      <thead >
        <tr>
          <th>Order No</th>
          <th >Requester</th>
          <th>Date Requested</th>
          <th>Purpose</th>
          <th>Approval Chain</th>
          <th v-if="item.project">Project</th>
          <th v-if="item.vendor">
          <span
           v-if="item?.type?.toLowerCase().includes('voucher')"
          >
           Beneficiary
          </span>
          <span v-else>
           Vendor
          </span>
          </th>
          <th>Awaits</th>
          <th>Cost</th>
          <th   v-if="user_permissions.includes('can_print_lpo')">Action</th>
        </tr>
      </thead>
      <tbody>
        <tr >
          <td>{{ item?.approval }}</td>
          <td >{{ item.requester }}</td>
          <td>{{ item?.date }}</td>
          <td>{{item?.type }}</td>
          <td>{{item?.purpose }}</td>
          <td v-if="item.project">{{ item.project }}</td>
          <td v-if="item.vendor">{{ item.vendor }}</td>
          <td>{{ item?.awaits }}</td>
          <td>{{ item?.cost }}</td>
          <td>
              <a 
                v-if="user_permissions.includes('can_print_lpo') && (item?.status?.name === 'approved' || item?.status?.name === 'paid')  "
                href="javascript:void(0);" @click="printGeneralOrder(item,item.vendor_data)" class="text-success me-2">
  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-printer" viewBox="0 0 16 16">
    <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z"/>
    <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2H5zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4V3zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2H5zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1z"/>
  </svg>
</a>
<button 
@click="toggleApproval()"
v-if="item.purpose === 'Labour Request' && user_permissions.includes('can_create_labour_voucher')"
class="btn btn-outline-primary">Create Voucher</button>
<router-link :to="`/report/labour-payments/${item.id}`" v-if="item.purpose === 'Labour Request'" class="text-primary">
  view History
</router-link>
          </td>
        </tr>
    <tr v-if="item.description ">
      <td colspan="8">
        <b> Brief Description : </b>
        {{ item.description }}
        <span v-if="approval_description"> ( {{ approval_description }} )</span>
      </td>

    </tr>
      <tr v-if="item.remark">
      <td colspan="8"> <b> Remark :  </b> {{ item.remark }}</td>
    </tr>
      </tbody>
    </table>

    </div>
    </div>
    </div>

       
          <div 
           v-if="item.status?.name === 'approved' && user_permissions.includes('can_complete_transaction')"
           class="d-flex justify-content-center mt-2 mb-2">
    <button class="btn btn-primary" type="button" :disabled="loading" @click="complete_transaction =! complete_transaction">
      <span v-if="loading" v-html="loadingSpinner()"></span>
      <span v-else>Paid</span>
    </button>
</div>
 <form v-if="complete_transaction" @submit.prevent="completeTransaction(item.id)">
    <div class="input-group container"  >
  <input type="text" class="form-control"
   placeholder="Enter Any remark "
    v-model="remark">
  <button 
  class="btn btn-primary me-2" type="submit" :disabled="loading">
  <span  v-if="loading" v-html="loadingSpinner()"></span>
  <span v-else>Save</span>
  </button>
</div>
 </form>


 

        <div class="modal-body">

          <div class="d-flex justify-content-center mb-2"  v-if="((user_role_id == awaiter.role_id && awaits !=='no one')) || (user_permissions.includes('is_md') && item.status?.name !== 'paid')">    
    <div class="btn-group" >
            <button class="btn btn-primary me-2" @click="status = 3" :disabled="spinner">
              Check 
            </button>
            <button class="btn btn-danger me-2" @click="status = 4" :disabled="spinner">
              Reject
            </button>

            <button class="btn btn-info me-2" @click="status = 14" :disabled="spinner">
              Remark
            </button>

            
            <button class="btn btn-warning" @click="status = 17" :disabled="spinner">
              On Hold
            </button>

          </div>
            </div>
   
      <!-- <p class="text-center">{{ status == 3 ? 'Enter Approval Remark' : status == 4 ? 'Enter Rejection Remark' : 'Enter Remark' }}</p> -->
      <div v-if="status" class="mb-2 text-center">
        <span
          class="badge rounded-pill"
          :class="{
            'bg-danger': status == 4,
            'bg-success': status == 3,
            'bg-info': status == 14,
            'bg-warning': status == 17
          }"
          style="font-size: 1rem;"
        >
          {{
            status == 3
              ? 'Check / Approve Request'
              : status == 4
              ? 'Reject Request'
              : 'Fill Remark'
          }}
        </span>
      </div>

<div class="input-group"  v-if="status">
  <input type="text" class="form-control"
    :placeholder="status == 3 ? 'Enter Approval Remark' : status == 4 ? 'Enter Rejection Remark' : 'Enter Remark'" 
    v-model="remark">
  <button 
     @click="onApproveClick()"
  class="btn btn-primary me-2" type="button" :disabled="spinner">
  <span v-if="spinner" v-html="loadingSpinner()"></span>
  <span v-else>Save</span>
  </button>
</div>


       
      
   
        <div class="custom-table table3 table-responsive">
          <table class="table">
            <thead>
              <tr>
                <th v-for="chain in approvalChain" :key="chain.role_id || chain.position || chain.name">
                  {{ chain.name }} <br/>
                </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td
                  v-for="(role, idx) in approvalChain"
                  :key="role.role_id || role.position || idx"
                  :class="role?.status?.color ? 'text-' + role.status.color : 'text-danger'"
                >
                <span> {{ role?.status?.name ?? 'Unchecked' }}</span><br/>
                <span class="text-dark" v-if="role.approved_at">Checked At  :{{ formatDate(role.approved_at) }} </span>  <br>
                <span class="text-dark" v-if="role.remark">Remark :  {{ role.remark }}</span> 
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <!-- approval items starts -->
        <h4 class="text-center" style="font-size: 1.3rem;">APPROVAL ITEMS</h4>
        <div class="d-flex justify-content-center mb-3">
          <input
            v-if="create_approval"
            type="text"
            class="form-control"
            style="max-width: 350px; font-size: 1rem;"
            placeholder="Enter Brief Description "
            v-model="description"
          >
        </div>
        <div class="col-xl-12 col-lg-12 col-sm-12 layout-spacing">
          <div class="panel p-0">
            <div class="custom-table table3 table-responsive">
              <table class="table table-bordered table-striped">
      <thead >
        <tr>
          <th>ID</th>
          <th>Item</th>
          <th>Decription</th>
          <th>Quantity</th>
          <th>Rate</th>
          <th>Total</th>
          <th>Receiver</th>
          <th   v-if="create_approval" class="text-center" >select</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="reqItem in requisitionItems">
           <td>{{ reqItem.order_item_id }}</td>
          <td>{{ reqItem.item }}</td>
          <td>
               <router-link class="btn btn-outline-primary" :to="`/requisition-budget/${reqItem.requisition_items.item_id}`"  v-if ="item.requisition_type ==='budget' && !reqItem.requisition_items.asset_id">  
               {{ reqItem.description }}
              </router-link>
              <span v-else> 
                <span v-if="reqItem.selected">
                     {{ reqItem.description }} ( {{ reqItem.request_description }} )
                </span>
                <span v-else>
                       {{ reqItem.description }}
                </span>
              
              </span>
           </td>
          <td @click="editApprovalItem(reqItem.id)">{{ reqItem.quantity}}
            <span v-if="reqItem?.requested_sum">
             <br> 
            Requested : {{ reqItem?.requested_sum }}
            <br>
            Balance :{{ (reqItem.quantity - reqItem?.requested_sum).toFixed(2) }}  
            </span>

            <span v-if="reqItem.selected"> <br>Requesting : {{ reqItem.requesting_quantity }} </span>
            <input v-if="edit_item == reqItem.id" type="number" class="form-control" placeholder="Quantity" step="any" v-model="reqItem.quantity">
          </td>
          <td @click="editApprovalItem(reqItem.id)">
            {{ reqItem.rate }}
          <input v-if="edit_item == reqItem.id" type="number" class="form-control" placeholder="Quantity" step="any" v-model="reqItem.rate_original">

          </td>
          <td>

            <span>
              <!-- {{ reqItem.total }}<br> -->
              {{ formatNumber(reqItem.quantity * reqItem.rate_original) }}<br>
            </span>
            <span v-if="reqItem.selected">
                Requesting : {{ formatNumber(reqItem.requesting_quantity * reqItem.rate_original) }}
            </span>

          </td>
          <td>{{ reqItem.receiver ?? reqItem.beneficiary }}</td>
          <td  
          v-if="create_approval"
          class="text-center" >
                <svg
     v-if="reqItem.selected"
      @click="reqItem.selected =! reqItem.selected"
      xmlns="http://www.w3.org/2000/svg" width="26" height="26"
      fill="currentColor" class="bi bi-check2-circle" viewBox="0 0 16 16">
      <path d="M2.5 8a5.5 5.5 0 0 1 8.25-4.764.5.5 0 0 0 .5-.866A6.5 6.5 0 1 0 14.5 8a.5.5 0 0 0-1 0 5.5 5.5 0 1 1-11 0"/>
      <path d="M15.354 3.354a.5.5 0 0 0-.708-.708L8 9.293 5.354 6.646a.5.5 0 1 0-.708.708l3 3a.5.5 0 0 0 .708 0z"/>
    </svg>

    <svg
    v-else
     @click="reqItem.selected =! reqItem.selected"
      xmlns="http://www.w3.org/2000/svg" width="26" height="26"
      fill="currentColor" class="bi bi-circle" viewBox="0 0 16 16">
      <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14m0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16"/>
    </svg>
        <input v-if="reqItem.selected" type="number" placeholder="enter quantity" class="form-control w-75" step="any"
         v-model="reqItem.requesting_quantity"
          @input="reqItem.requesting_quantity = enforceMax(reqItem.requesting_quantity,(reqItem.quantity - reqItem?.requested_sum).toFixed(2))"
         >

          <input v-if="reqItem.selected" type="text" placeholder="enter Description" class="form-control w-75 mt-1" v-model="reqItem.request_description">
          <input v-if="reqItem.selected" type="text" placeholder="enter Beneficiary" class="form-control w-75 mt-1"   v-model="reqItem.receiver">

          </td>
          <td>
                      <router-link  :to="`/assets/fuel-statement/${reqItem.requisition_items.asset_id}`"  v-if ="reqItem.requisition_items.asset_id" class="me-2">     
                            <button class="btn btn-outline-primary me-2">Diesel </button>
                      </router-link>  

                       <router-link  :to="`/requisition-budget/${reqItem.requisition_items.item_id}`"  v-if ="item.requisition_type ==='budget'">     
                            <button class="btn btn-outline-primary me-2">Budget</button>
                      </router-link>  
                        <router-link  :to="`/jobcard/${item.source_id}/${reqItem.requisition_items.asset_id}`"  v-if ="item.requisition_type ==='jobcard'" class="me-2">     
                            <button class="btn btn-outline-primary me-2">Jobcard</button>
                      </router-link>  
