<template>
     <form @submit.prevent class="input-group container">
    <input
      type="text"
      class="form-control border-end-0"
      placeholder="Search a word..."
      v-model="filter"
    >
    <span class="input-group-text bg-white border-start-0">
      <svg xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="currentColor" class="bi bi-search" viewBox="0 0 16 16">
        <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001l3.85 3.85a1 1 0 0 0 1.415-1.415l-3.85-3.85zm-5.442.656a5 5 0 1 1 
        0-10 5 5 0 0 1 0 10z"/>
      </svg>
    </span>
  </form>
</template>

<script setup>
import { ref, watch } from 'vue'


const emit = defineEmits(['update:filterValue'])
const props = defineProps({
  filterValue: String
})
const filter = ref(props.filterValue || null)

watch(filter, val => emit('update:filterValue', val))
</script>
