export function globalSearch(data, keyword) {
  const lowerKeyword = keyword.toLowerCase().trim()
  if (!lowerKeyword) return data

  const containsKeyword = obj => {
    if (obj == null) return false
    if (typeof obj === 'string') return obj.toLowerCase().includes(lowerKeyword)
    if (Array.isArray(obj)) return obj.some(containsKeyword)
    if (typeof obj === 'object')
      return Object.values(obj).some(containsKeyword)
    return false
  }

  return data.filter(item => containsKeyword(item))
}